<template>
    <div>
        <i-button type="ghost" @click="handleDefault"></i-button>
        <i-button type="ghost" @click="handleSuccess"></i-button>
        <i-button type="ghost" @click="handleWarning"></i-button>
        <i-button type="ghost" @click="handleError"></i-button>
        <i-button type="ghost" @click="handleDuration"></i-button>
    </div>
</template>
<script>
export default {
  methods: {
    handleDefault() {
      this.$Message({
        content: ""
      });
    },
    handleSuccess() {
      this.$Message({
        content: "",
        type: "success"
      });
    },
    handleWarning() {
      this.$Message({
        content: "",
        type: "warning"
      });
    },
    handleError() {
      this.$Message({
        content: "",
        type: "error"
      });
    },
    handleDuration() {
      this.$Message({
        content: " 5 ",
        duration: 5
      });
    }
  }
};
</script>


