<template>
    <div>
        <div style="margin-top: 100px">
            <i-button type="ghost" @click="handleOpen1"></i-button>
            <i-button type="ghost" @click="handleOpen2"></i-button>
        </div>

        <i-action-sheet :visible="visible1" :actions="actions1" show-cancel @cancel="handleCancel1" @click="handleClickItem1"></i-action-sheet>

        <i-action-sheet :visible=" visible2" :actions=" actions2 " show-cancel @cancel="handleCancel2" @click="handleClickItem2" :mask-closable="false">
            <div slot="header" style="padding: 16px">
                <div style="color: #444;font-size: 16px"></div>
                <span></span>
            </div>
        </i-action-sheet>
    </div>
</template>
<script>
export default {
  data() {
    return {
      visible1: false,
      visible2: false,
      actions1: [
        {
          name: "1"
        },
        {
          name: "2"
        },
        {
          name: "",
          icon: "share"
        }
      ],
      actions2: [
        {
          name: "",
          color: "#ed3f14"
        }
      ]
    };
  },
  methods: {
    handleOpen1() {
      this.visible1 = true;
    },

    handleCancel1() {
      this.visible1 = false;
    },

    handleOpen2() {
      this.visible2 = true;
    },

    handleCancel2() {
      this.visible2 = false;
    },

    handleClickItem1(index) {
      index += 1;
      this.$Message({
        content: "" + index
      });
    },

    handleClickItem2() {
      const action = [...this.actions2];
      action[0].loading = true;

      this.actions2 = action;

      setTimeout(() => {
        action[0].loading = false;
        this.visible2 = false;
        this.actions2 = action;
        this.$Message({
          content: "",
          type: "success"
        });
      }, 2000);
    }
  }
};
</script>


