<template>
    <div>
        <i-panel title=""></i-panel>

        <i-tabs v-model="current" @change="handleChange">
            <i-tab title="2"></i-tab>
            <i-tab title="3"></i-tab>
            <i-tab title="1"></i-tab>
        </i-tabs>

        <i-panel title=""></i-panel>

        <i-tabs v-model="current" color="#f759ab" @change="handleChange">
            <i-tab title="1"></i-tab>
            <i-tab title="2"></i-tab>
            <i-tab title="3"></i-tab>
        </i-tabs>

        <i-panel title=""></i-panel>

        <i-tabs v-model="current" @change="handleChange">
            <i-tab title="1" :count="3"></i-tab>
            <i-tab title="2"></i-tab>
            <i-tab title="3" dot></i-tab>
        </i-tabs>

        <i-panel title=""></i-panel>

        <i-tabs v-model="current_scroll" scroll @change="handleChangeScroll">
            <i-tab title="1"></i-tab>
            <i-tab title="2"></i-tab>
            <i-tab title="3"></i-tab>
            <i-tab title="4"></i-tab>
            <i-tab title="5"></i-tab>
            <i-tab title="6"></i-tab>
            <i-tab title="7"></i-tab>
            <i-tab title="8"></i-tab>
            <i-tab title="9"></i-tab>
        </i-tabs>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                current: 0,
                current_scroll: 0
            }
        },
        methods: {
            handleChange(index) {
                console.log(this.current)
            },

            handleChangeScroll(index) {
                console.log(index)
            }
        }
    }
</script>


