<template>
    <div>
        <i-panel class="cell-panel-demo">
            <i-cell title=""></i-cell>
        </i-panel>

        <i-panel class="cell-panel-demo">
            <i-cell title="" is-link value=""></i-cell>
        </i-panel>

        <i-panel class="cell-panel-demo">
            <i-cell title="" label="" value=""></i-cell>
        </i-panel>

        <i-panel class="cell-panel-demo" title=" icon  Cell">
            <i-cell title="" value="">
                <i-icon type="like_fill" slot="icon" />
            </i-cell>
        </i-panel>

        <i-panel class="cell-panel-demo" title=" cell">
            <i-cell title="" is-link></i-cell>
        </i-panel>

        <i-panel class="cell-panel-demo">
            <i-cell title="" is-link url="/pages/index/index"></i-cell>
        </i-panel>

        <i-panel class="cell-panel-demo" title="cell ">
            <i-cell-group>
                <i-cell title="" is-link></i-cell>
                <i-cell title="" is-link url="/pages/index/index "></i-cell>
                <i-cell title=" footer " is-link url="/pages/index/index" only-tap-footer></i-cell>
                <div v-if="switch1">
                    <i-cell title="" label="" value=""></i-cell>
                    <i-cell title="">
                        <i-input title=""></i-input>
                        <i-input title=""></i-input>
                    </i-cell>
                </div>
                <i-cell title="">
                    <i-switch :value="switch1" @change="onChange" slot="footer">
                        <i-icon type="right" slot="open"></i-icon>
                        <i-icon type="close" slot="close"></i-icon>
                    </i-switch>
                </i-cell>
            </i-cell-group>
        </i-panel>

        <i-panel class="cell-panel-demo">
            <i-cell title="" label="" value=""></i-cell>
        </i-panel>
    </div>
</template>
<script>
export default {
  data() {
    return {
      switch1: true
    };
  },
  methods: {
    onChange(value) {
      this.switch1 = value;
    },
  }
};
</script>

<style lang="less">
.cell-panel-demo {
  display: block;
  margin-top: 15px;
}
</style>



