<template>
    <div class="i-swipeout-demo">
        <div class="i-swipeout-demo-title"></div>
        <div class="i-swipeout-demo-des">
            : 1uncloseabletrue,toggle2
            2actionwidth
        </div>
        <i-swipeout class="i-swipeout-demo-item" :actions="actions">
            <div slot="content">
                <i-cell class="i-cell-padding" title="" label="">
                </i-cell>
            </div>
        </i-swipeout>
        <i-swipeout class="i-swipeout-demo-item" :actions="actions" :toggle="toggle2" unclosable>
            <div slot="content">
                <i-cell class="i-cell-padding" title="" label="">
                </i-cell>
            </div>
        </i-swipeout>
        <i-swipeout class="i-swipeout-demo-item" :actions="actions" :toggle="toggle2" unclosable @change="handlerCloseButton">
            <div slot="content">
                <i-cell class="i-cell-padding" title="" label="">
                </i-cell>
            </div>
        </i-swipeout>


        <div class="i-swipeout-demo-title">Button</div>
        <div class="i-swipeout-demo-des">: 160px</div>
        <i-swipeout class="i-swipeout-demo-item" :operateWidth="210">
            <div slot="content">
                <i-cell class="i-cell-padding" title="" label="">
                </i-cell>
            </div>
            <div slot="button" class="i-swipeout-demo-button-group">
                <div class="i-swipeout-demo-button"></div>
                <div class="i-swipeout-demo-button"></div>
                <div class="i-swipeout-demo-button"></div>
            </div>
        </i-swipeout>
        <i-swipeout class="i-swipeout-demo-item" :operateWidth="180">
            <div slot="content">
                <i-cell class="i-cell-padding" title="" label="">
                </i-cell>
            </div>
            <div slot="button" class="i-swipeout-demo-button-group" style="background:#2db7f5;">
                <div class="i-swipeout-demo-button" style="width:60px">
                    <i-icon :size="32" type="like_fill"></i-icon>
                </div>
                <div class="i-swipeout-demo-button" style="width:60px">
                    <i-icon :size="32" type="share_fill"></i-icon>
                </div>
                <div class="i-swipeout-demo-button" style="width:60px">
                    <i-icon :size="32" type="delete_fill"></i-icon>
                </div>
            </div>
        </i-swipeout>


        <div class="i-swipeout-demo-title">actionsheet</div>
        <i-action-sheet :visible=" visible2 " :actions=" actions2 " show-cancel @cancel="handleCancel2" @click="handleClickItem2"
            :mask-closable=" false ">
            <div slot="header" style="padding: 16px">
                <div style="color: #444;font-size: 16px"></div>
                <p></p>
            </div>
        </i-action-sheet>
        <i-swipeout class="i-swipeout-demo-item" :operateWidth="180" :unclosable="true" :toggle="toggle">
            <div slot="content">
                <div class="i-swipeout-image">
                    <i-icon :size="20" color="#FFFFFF" type="feedback_fill"></i-icon>
                </div>
                <div class="i-swipeout-des">
                    <div class="i-swipeout-des-h2"></div>
                    <div class="i-swipeout-des-detail">7.</div>
                </div>
            </div>
            <div slot="button" class="i-swipeout-demo-button-group" style="background:#2db7f5;">
                <div class="i-swipeout-demo-button" style="width:60px" @click="actionsTap">
                    <i-icon :size="32" type="like_fill"></i-icon>
                </div>
                <div class="i-swipeout-demo-button" style="width:60px" @click="actionsTap">
                    <i-icon :size="32" type="share_fill"></i-icon>
                </div>
                <div class="i-swipeout-demo-button" style="width:60px" @click="actionsTap">
                    <i-icon :size="32" type="delete_fill"></i-icon>
                </div>
            </div>
        </i-swipeout>

        <div class="i-swipeout-demo-title"></div>
        <i-swipeout class="i-swipeout-demo-item" :actions="actions">
            <div slot="content">
                <div class="i-swipeout-image" style="background:#ff9900;">
                    <i-icon :size="20" color="#FFFFFF" type="coupons_fill" />
                </div>
                <div class="i-swipeout-des">
                    <div class="i-swipeout-des-h2"></div>
                    <div class="i-swipeout-des-detail">7.</div>
                </div>
            </div>
        </i-swipeout>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                trans: 0,
                visible2: false,
                toggle: false,
                toggle2: false,
                actions2: [{
                    name: '',
                    color: '#ed3f14'
                }],
                actions: [{
                        name: '',
                        color: '#fff',
                        fontsize: 20,
                        width: 100,
                        icon: 'like',
                        background: '#ed3f14'
                    },
                    {
                        name: '',
                        width: 100,
                        color: '#80848f',
                        fontsize: 20,
                        icon: 'undo'
                    }
                ]
            }
        },
        methods: {
            handleCancel2() {
                this.visible2 = false
                this.toggle = this.toggle ? false : true
            },
            handleClickItem2() {
                const action = [...this.actions2];
                action[0].loading = true;
                this.actions2 = action

                setTimeout(() => {
                    action[0].loading = false;
                    this.visible2 = false
                    this.actions2 = action
                    this.toggle = this.toggle ? false : true

                }, 2000);
            },
            handlerCloseButton(index) {
                console.log(index)
                this.toggle2 = this.toggle2 ? false : true
            },
            actionsTap() {
                this.visible2 = true
            }
        }
    }
</script>

<style lang="less">
    .i-swipeout-demo-item {
        border-bottom: #333 solid 1px;
    }

    .i-cell-padding {
        padding: 5px 0px !important;
        font-size: 14px;
    }

    .i-swipeout-demo-title {
        height: 30px;
        padding: 0 15px;
        line-height: 30px;
        margin-top: 20px;
        margin-bottom: 5px;
    }

    .i-swipeout-demo-des {
        padding: 0 15px;
        margin-bottom: 10px;
        font-size: 12px;
        color: #ff9900;
    }

    /**/
    .i-swipeout-image {
        float: left;
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        border-radius: 40px;
        background: #2d8cf0;
    }

    .i-swipeout-des {
        margin-left: 50px;
    }

    .i-swipeout-des-detail {
        font-size: 12px;
        word-break: break-all;
        color: #80848f;
    }


    /**/
    .i-swipeout-demo-button-group {
        height: 100%;
        width: 100%;
    }

    .i-swipeout-demo-button {
        width: 70px;
        float: left;
        display: flex;
        height: 100%;
        justify-content: center;
        background: #2d8cf0;
        color: #fff;
        align-items: center;
    }
</style>


