<template>
  <div>
    <i-button type="ghost" @click="handleText"></i-button>
    <i-button type="ghost" @click="handleSuccess"></i-button>
    <i-button type="ghost" @click="handleWarning"></i-button>
    <i-button type="ghost" @click="handleError"></i-button>
    <i-button type="ghost" @click="handleLoading">Loading</i-button>
    <i-button type="ghost" @click="handleIcon"></i-button>
    <i-button type="ghost" @click="handleImage"></i-button>
    <i-button type="ghost" @click="handleMask"></i-button>
  </div>
</template>
<script>
export default {
  methods: {
    handleText() {
      this.$Toast({ content: "" });
    },
    handleSuccess() {
      this.$Toast({
        content: "",
        type: "success"
      });
    },
    handleWarning() {
      this.$Toast({
        content: "",
        type: "warning"
      });
    },
    handleError() {
      this.$Toast({
        content: "",
        type: "error"
      });
    },
    handleLoading() {
      this.$Toast({
        content: "",
        type: "loading"
      });
    },
    handleIcon() {
      this.$Toast({
        content: "",
        icon: "praise"
      });
    },
    handleImage() {
      this.$Toast({
        content: "",
        image: "https://i.loli.net/2017/08/21/599a521472424.jpg"
      });
    },
    handleMask() {
      this.$Toast({
        content: "5",
        icon: "prompt",
        duration: 0,
        mask: false
      });
    }
  }
};
</script>



