<template>
    <div>
        <div style="margin-top: 100px;">
            <i-button @click="handleOpen1"></i-button>
            <i-button @click="handleOpen2"></i-button>
            <i-button @click="handleOpen3"></i-button>
            <i-button @click="handleOpen4"></i-button>
            <i-button @click="handleOpen5"></i-button>
        </div>

        <i-modal title="" :visible="visible1" @ok="handleClose1" @cancel="handleClose1">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
        </i-modal>

        <i-modal :visible="visible2" @ok="handleClose2" @cancel="handleClose2">
            <p></p>
        </i-modal>

        <i-modal title="" :visible="visible3" :actions="actions3" @click="handleClick3">
            <p></p>
        </i-modal>

        <i-modal title="" :visible="visible4" :actions="actions4" action-mode="vertical" @click="handleClick4">

        </i-modal>

        <i-modal title="" :visible="visible5" :actions="actions5" @click="handleClick5">
            <p></p>
        </i-modal>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                    visible1: false,
                    visible2: false,
                    visible3: false,
                    visible4: false,
                    visible5: false,
                    actions3: [{
                            name: '',
                            color: '#2d8cf0',
                        },
                        {
                            name: '',
                            color: '#19be6b'
                        },
                        {
                            name: ''
                        }
                    ],
                    actions4: [{
                            name: '1'
                        },
                        {
                            name: '2',
                            color: '#ff9900'
                        },
                        {
                            name: '3',
                            icon: 'search'
                        }
                    ],
                    actions5: [{
                            name: ''
                        },
                        {
                            name: '',
                            color: '#ed3f14',
                            loading: false
                        }
                    ]
            }
        },
        methods: {
            handleOpen1() {
                this.visible1 = true
            },

            handleClose1() {
                this.visible1 = false
            },

            handleOpen2() {
                this.visible2 = true

            },

            handleClose2() {
                this.visible2 = false
            },

            handleOpen3() {
                this.visible3 = true
            },

            handleClick3(index) {
                if (index === 0) {
                    this.$Message({
                        content: ''
                    });
                } else if (index === 1) {
                    this.$Message({
                        content: ''
                    });
                }
                this.visible3 = false
            },

            handleOpen4() {
                this.visible4 = true
            },

            handleClick4() {
                this.visible4 = false
            },

            handleOpen5() {
                this.visible5 = true
            },

            handleClick5(index) {
                if (index === 0) {
                    this.visible5 = false
                } else {
                    const action = [...this.actions5];
                    action[1].loading = true;

                    this.actions5 = action

                    setTimeout(() => {
                        action[1].loading = false;
                        this.visible5 = false
                        this.actions5 = action
                        this.$Message({
                            content: '',
                            type: 'success'
                        });
                    }, 2000);
                }
            }
        }
    }
</script>


