<template>
  <div>
    <i-panel title="">
      <i-input v-model="value1" title="" autofocus placeholder="" />
      <i-input v-model="value2" type="number" title="" placeholder="" />
      <i-input v-model="value3" type="textarea" autosize title="" placeholder="(50)" :maxlength="50" />
      <i-input v-model="value4" title="" disabled />
      <i-input v-model="value8" readonly title=""></i-input>
    </i-panel>

    <i-panel title="">
      <i-input v-model="value5" placeholder="" />
    </i-panel>

    <i-panel title="">
      <i-input @change="changeValue" @focus="focus" @blur="blur" v-model="value6" type="number" right title="" mode="wrapped" placeholder="" />
      <i-input v-model="value7" type="number" right error title="" mode="wrapped" placeholder="" />
    </i-panel>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: ""
    };
  },
  methods: {
    changeValue(value) {
      console.log(value);
    },
    focus(e) {
      console.log(e);
    },
    blur(e) {
      console.log(e);
    }
  }
};
</script>


