<template>
    <div style="margin:10px">
        <i-panel title=""></i-panel>

        <i-button @click="handleClick" inline></i-button>
        <i-button @click="handleClick" inline type="primary"></i-button>
        <i-button @click="handleClick" inline type="success"></i-button>

        <i-button @click="handleClick"></i-button>
        <i-button @click="handleClick" type="primary">Primary</i-button>
        <i-button @click="handleClick" type="ghost">Ghost</i-button>
        <i-button @click="handleClick" type="info">Info</i-button>
        <i-button @click="handleClick" type="success">Success</i-button>
        <i-button @click="handleClick" type="warning">Warning</i-button>
        <i-button @click="handleClick" type="error">Error</i-button>

        <i-panel title=""></i-panel>

        <i-button @click="handleClick" type="primary" size="large"></i-button>
        <i-button @click="handleClick" type="primary" size="default"></i-button>
        <i-button @click="handleClick" type="primary" size="small"></i-button>

        <i-panel title=""></i-panel>

        <i-button @click="handleClick" type="success" shape="circle" size="large"></i-button>
        <i-button @click="handleClick" type="success" shape="circle"></i-button>
        <i-button @click="handleClick" type="success" shape="circle" size="small"></i-button>

        <i-button @click="handleClick" type="primary" loading></i-button>

        <i-button @click="handleClick" type="primary" disabled></i-button>
    </div>
</template>
<script>
export default {
  methods: {
    handleClick() {}
  }
};
</script>



